.card-tech{
    box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
    margin-bottom: 1rem;
    margin-right: 1rem;
    width: 10rem;
}

.card-img-top{
    height: 10rem;
}

.card-tech-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.card-footer {   
    flex: 1;
}

.card{
    border: none !important;
}

.cst-tablet-font-size{
    font-size: medium !important;
}

.card-tech-title{
    font-weight: bold;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
    .card-tech-container{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
    .card-tech {
        box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
        margin-bottom: 1rem;
        margin-right: 0;
        width: 60vw;
    }
    .card-img-top{
        height: 60vw;
    }
  }