.notfound-banner-container{
    min-height: 40vh !important;
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
}

.notfound-banner{
    background-color: white;
    border-radius: 12px;
    position: relative;
    bottom: -7rem;
    padding: 30px;
    flex: 0.3;
    flex-direction: column;
    display: flex;
}

.notfound-image{
    width: 80% !important;
}

.notfound-message{
    color:#3295FF!important;
    font-weight: 600;
    margin-top: 1.5rem;    
    font-size: large;
}

.notfound-help-text{
    font-style: italic;
}