.contact-banner-container{
    min-height: 40vh !important;
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
}

.contact-banner{
    background-color: white;
    border-radius: 12px;
    position: relative;
    bottom: -7rem;
    padding: 30px;
    flex: 0.8;
    flex-direction: column;
    display: flex;
}

.contact-note{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    display: flex;
}

.alert {
    padding: .5rem 0.5rem;
    margin-bottom: 0 !important;
}

