.banner-container{
    min-height: 70vh !important;
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
}

.banner{
    background-color: white;
    border-radius: 12px;
    position: relative;
    bottom: -7rem;
    padding: 30px;
    flex: 0.8;
}

.banner-image{
    width: 100%;
    border-radius: 12px;
}

.home-greeting-text{
    font-weight: 350 !important;
}

.home-profile-text{
    font-weight: 450 !important;
}