.about-banner-container{
    min-height: 40vh !important;
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
}

.about-banner{
    background-color: white;
    border-radius: 12px;
    position: relative;
    bottom: -7rem;
    padding: 30px;
    flex: 0.8;
}

.about-education-container{
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
    margin-top: 20rem;
    flex-direction: column;
    align-content: center;
}

.about-education-header-container{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    width: 100%;
}

.about-education{
    background-color: white;
    border-radius: 12px;
    position: relative;
    top: -7rem;
    padding: 30px;
    flex: 0.8;
}

.about-employment-container{
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
    margin-top: 14rem;
    flex-direction: column;
    align-content: center;
}

.about-employment-header-container{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    width: 100%;
}

.about-employment{
    background-color: white;
    border-radius: 12px;
    position: relative;
    top: -7rem;
    padding: 30px;
    flex: 0.8;
}

.about-education-degree{
    margin-top: -20px !important;
    margin-left: 2.5rem;
}

.about-employment-title{
    margin-top: -20px !important;
    margin-left: 2rem;
}

.about-current-education-text{
    font-weight: 450 !important;
}

.about-current-employment-text{
    font-weight: 450 !important;
}

