.projects-banner-container{
    min-height: 40vh !important;
    background: #84BFFF;
    background: -webkit-linear-gradient(top, #84BFFF, #FFFFFF);
    background: -moz-linear-gradient(top, #84BFFF, #FFFFFF);
    background: linear-gradient(to bottom, #84BFFF, #FFFFFF);
    justify-content: center;
    display: flex;
    padding-right: 0;
    padding-left: 0;
}

.projects-banner{
    background-color: white;
    border-radius: 12px;
    position: relative;
    bottom: -7rem;
    padding: 30px;
    flex: 0.8;
    flex-direction: column;
    display: flex;
}

.card-project{
    box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12);
    margin-bottom: 1.5rem;
}

.card-projects-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.project-item-title{
    color: #6c757d !important;
    font-weight: 500 !important;
    font-style: italic !important;
}